import React from 'react'
import { motion } from 'framer-motion'
import { Route, Routes } from 'react-router'
import catAbout from '../assets/catimages/cat-about.svg'
import catHome from '../assets/catimages/cat-home.svg'
import catProject from '../assets/catimages/cat-project.svg'
// import catPage1 from '../assets/catimages/cat-page1.svg'
// import catPage2 from '../assets/catimages/cat-page2.svg'
import styled from 'styled-components'

const HeaderCat = styled.div`
    position:relative;
    width: 90vw;
    margin-top:5vh;
    align-self: center;
    height: 10vh;
    flex-shrink: 0;

img{
    position: absolute;
    bottom:0;
    left:0;
}
`

function Header() {

    return (
        <HeaderCat>
            <Routes>
                <Route path="/" element={<motion.div key={"cat-home"} initial={{ opacity: 0.1 }} animate={{ opacity: 1 }} transition={{ duration: 1, delay: 0.2 }} >
                    <img src={catHome} alt="Cat"></img>
                </motion.div>} />
                <Route path="/projects" element={<motion.div key={"cat-projects"}  initial={{ opacity: 0.1 }} animate={{ opacity: 1 }} transition={{ duration: 1, delay: 0.2 }} >
                    <img src={catProject} alt="Cat"></img>
                </motion.div>} />
                <Route path="/about" element={<motion.div key={"cat-about"} initial={{ opacity: 0.1 }} animate={{ opacity: 1 }} transition={{ duration: 1, delay: 0.2 }} >
                    <img src={catAbout} alt="Cat"></img>
                </motion.div>} />
            </Routes>
        </HeaderCat>
    )
}

export default Header
