import React from 'react'
import { motion } from 'framer-motion'
import styled from 'styled-components'


const Button = styled(motion.a)`
    background-color: white;
    font-family: var(--main-font);
    text-align: center;
    padding: calc(0.675rem/2 + 0.5vw);
    color:black;
    cursor: pointer;
    align-self: flex-end;
    font-size:calc(0.5rem + 0.5vw);
    border:none;
    z-index:5;
`

function ButtonContact() {
    return (
        <Button whileHover={{
            scale: 1.05,
            transition: { duration: 0.1 },
        }}
            whileTap={{ scale: 0.9 }} href="mailto:leonardo.fernandes2108@hotmail.com?subject=Hey, I've just seen your portfolio!">
            Get in Touch
        </Button>
    )
}

export default ButtonContact
