import React from 'react'
import styled from 'styled-components'
import HexBG from '../assets/generalimages/Hexagon.svg'

const ProjectUniqueItem = styled.div`
    position: relative;
    aspect-ratio: 1;
    color: white;
    display:flex;
    flex-direction:column;
    align-items:center;
    gap: 1rem;
    cursor: pointer;
`

const ProjectNumber = styled.div`
    position: absolute;
    top:0;
    left:0;
    font-family:'Rationale';
    display: inline-flex;
    font-size:calc(3rem + 5vw);
    color: rgba(255, 255, 255, 0.13);
    font-weight: 500;
    transform: translate(-50%, -40%)
`

const ProjectBG = styled.div`
    position: absolute;
    width: 100%;
    height:100%;
    bottom: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-image:url(${HexBG});
    background-size: contain;
    background-position: center bottom;
    background-repeat: no-repeat;
`

const ProjectLogo = styled.div`
    display:flex;
    height: 125%;

img{
    width:100% ;
}
`

const ProjectHeader = styled.div`
    display:flex;
    position:relative;
    width:85%;
    height: 3rem;
    align-items:center;
    justify-content:center;
    background: linear-gradient(262.06deg, rgba(255, 255, 255, 0.048) 2.02%, rgba(25, 29, 75, 0.51) 78.72%, rgba(25, 91, 129, 0.342) 97.65%);
    border-radius: 1rem;
    z-index: 5;
    margin-top:1rem;
`

const ProjectTitle = styled.div`
    display:flex;
    flex-direction: column;
    gap:calc(0.1rem + 0.1vh);
    padding: calc(0.350rem + 0.25vw);
    padding-left: 0;
    font-size:calc(0.675rem + 0.5vw);

p{
    font-size: 0.4em;
}
`
const ProjectDetail = styled.div`
    font-family:var(--secundary-font);
    color: white;
    width: 80%;
    display: flex;
    position: relative;
    z-index: 5;
    gap: 0.5rem;
    flex: 1;
    margin-bottom: 1rem;
`
const ProjectText = styled.div`
    background: rgba(255, 255, 255, 0.26);
    border-radius: 1rem;
    padding: 0.5rem;
    font-size: 0.8rem;

`
const ProjectIcons = styled.div`
    background: rgba(255, 255, 255, 0.26);
    border-radius: 1rem;
    padding: 0.25rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;


img{
    width:20px;
}
`

const CategoryIndicator = styled.img`
    position:absolute;
    width:1rem;
    top:0;
    right:0;
    transform: translate(-50%,-50%)
`

function ProjectItem({ projectdata, activeproject }) {
    return (
        <ProjectUniqueItem onClick={() => activeproject(projectdata)}>
            <ProjectNumber>
                {projectdata.id}
            </ProjectNumber>
            <ProjectBG>
            </ProjectBG>
            <ProjectHeader>
                <CategoryIndicator src={require(`../assets/categoryimages/${projectdata.category}-indicator.svg`).default} alt={`${projectdata.category}-image`}></CategoryIndicator>
                <ProjectLogo>
                    <img src={require(`../assets/projectlogoimages/${projectdata.logo}`).default} alt={`${projectdata.title}-logo`}></img>
                </ProjectLogo>
                <ProjectTitle>
                    <h3>{projectdata.title}</h3>
                    <p>{projectdata.subtitle}</p>
                </ProjectTitle>
            </ProjectHeader>
            <ProjectDetail>
                <ProjectText>
                    <p>{projectdata.description}</p>
                </ProjectText>
                <ProjectIcons>
                    {(projectdata.tools).map( tool => <img key={tool} src={require(`../assets/toolslogo/${tool}-logo.svg`).default} alt={`${tool}-logo`}></img>)}
                </ProjectIcons>
            </ProjectDetail>
        </ProjectUniqueItem>
    )
}

export default ProjectItem
