import React from 'react'
import styled from 'styled-components'

const ActivePreview = styled.div`
    color: white;
    display: grid;
`
const PreviewHeader = styled.div``

const PreviewVideo= styled.div``

const Preview = ({ ActiveItem }) => {
    return (
        <div>
            {ActiveItem ?
                <ActivePreview>
                    <PreviewHeader>

                    </PreviewHeader>
                    <PreviewVideo>

                    </PreviewVideo>
                </ActivePreview> : <h1>'Hello'</h1>}
        </div>
    )
}

export default Preview
