import React from 'react'
import styled from 'styled-components'
import { useState } from 'react'

const ProjectSlider = styled.div`
    grid-area: slide;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;

div{
    background-color: white;
    width:10px;
    height:10px;
    border-radius:10px;
    cursor: pointer;
}
    `

function ProjectSlide({projectdata, max_page, currentlypage, newpage}) {

    let number_sliders=[...Array(Math.ceil(projectdata.length/max_page)).keys()]

    return (
        <ProjectSlider>
            {number_sliders.map(i => <div key={i}
            style={currentlypage === i ? {'background-color':'#5265FF'} : {'background-color':"#FFFFFF"}}
            onClick={() => newpage(i)}></div>)}
        </ProjectSlider>
    )
}

export default ProjectSlide
