import React from 'react'
import styled from 'styled-components'
import Social from './Social'
import SlideBar from './SlideBar'

const NavBar = styled.div`
    display: flex;
    position: relative;
    color: white;
    margin-bottom: 2vh;
    justify-content: space-between;
    align-items: center;
    width: 90vw;
    align-self: center;
    flex-shrink:0;
`

const Navbar = () => {
    return (
        <NavBar>
            <h1> myPortfolio </h1>
            <SlideBar />
            <Social />
        </NavBar>
    )
}

export default Navbar

