import React from 'react'
import styled from 'styled-components'


const Icons = styled.div`
    display: flex;
    flex-direction: row;

i{
    color:white;
    font-size: 1.5em;
    font-style: normal;
    font-weight: normal;
}

a:not(:last-child){
    margin-right: 0.5rem;
}

`

function Social() {
    return (
        <Icons className="social-tags">
            <a href="https://www.linkedin.com/in/leonardo-fernandes-02b706191/">
                <i className="fab fa-linkedin"></i>
            </a>
            <a href="https://github.com/lfernannd">
                <i className="fab fa-github"></i>
            </a>
        </Icons>
    )
}

export default Social
