import React from 'react'
import Preview from './Preview'
import ProjectItem from './ProjectItem'
import PortfolioData from '../PortfolioData'
import { useState , useEffect} from 'react'
import styled from 'styled-components'
import { motion } from 'framer-motion'
import ButtonCategory from './ButtonCategory'
import ProjectSlide from './ProjectSlide'

const ProjectPage = styled.div`
    flex:1;
    width:90vw;
    margin-left:5vw;
    align-self:flex-start;
    display: flex;
    justify-content:space-between;
    position:relative;
`

const ProjectsGrid = styled.div`
    position: relative;
    grid-area: grid;
    width:450px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-auto-rows: 0;
    gap: 1.5rem 4rem;
    place-items:center;
`

const ProjectControl = styled.div`
    position: relative;
    display: grid;
    height:100%;
    aspect-ratio:1;
    grid-template-columns:1fr 25px;
    grid-template-rows: 5px 1fr;
    grid-template-areas: 
        " buttons . "
        " grid slide";
    place-content:center;
    `

const ProjectButtons = styled.div`
    grid-area: buttons;
    position: relative;
    display: flex;
    color: white;
    top: -3rem;
    justify-content: space-around;
    align-items: center;
`

function Projects() {

    const [ActiveProject, setActiveProject] = useState(undefined);
    const [PortfolioItems, setPortfolioItems] = useState(PortfolioData);
    const Categories = [...new Set(PortfolioData.map(item => item.category))]
    Categories.unshift('All');

    const [ActiveFilter, setActiveFilter] = useState( new Array('All'));

    const [Page, setPage] = useState(0)
    const [PortfolioRender, setPortfolioRender] = useState(PortfolioItems)
    

    useEffect(() => {
        function PortfolioFiltered() {
            let FilteredCategories = ActiveFilter
            console.log(FilteredCategories)
            if (FilteredCategories.includes("All") && FilteredCategories.length === 1){
                console.log('a') 
                return setPortfolioItems(PortfolioData)
            } else {
                console.log('b')
                const FilteredPortfolio = PortfolioData.filter(project => FilteredCategories.includes(project.category))
                setPortfolioItems(FilteredPortfolio)
            }
        }    
        PortfolioFiltered()
        setPage(0)},[ActiveFilter]
        )

    useEffect(() => {
        setPortfolioRender(PortfolioItems.slice((Page*4),((Page*4) + 4)))
    }, [Page , PortfolioItems])

    return (
        <ProjectPage>
            <Preview ActiveItem={ActiveProject} />
            <ProjectControl>
            <ProjectButtons>
                {Categories.map(cat => <ButtonCategory key={cat} cat={cat} setfilter={setActiveFilter} activefilter={ActiveFilter}/>)}
            </ProjectButtons>
                <ProjectsGrid>
                    {PortfolioRender.map(project => <ProjectItem activeproject={setActiveProject} projectdata={project} key={project.id} />)}
                </ProjectsGrid>
            <ProjectSlide projectdata={PortfolioItems} max_page={4} currentlypage={Page} newpage={setPage}/>
            </ProjectControl>
        </ProjectPage>
    )
}

export default Projects


