import React from 'react'
import styled from 'styled-components'
import { NavLink } from 'react-router-dom'

const Slider = styled.div`
    display: flex;
    color: white;
    align-items: flex-start;

a{
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 0.6rem;
    font-size: 1rem;
}

a::visited{
    color: inherit;
}

a::after{
    margin-top: 0.3rem;
    display:block;
    content: '';
    align-self: center;
    width: 0;
    opacity: 0;
    height: 0.2rem;
    transition: width 0.5s, opacity 1s
}

a.CurrentPage::after{
    opacity: 1;
    background: #04EBEB;
    width: 100%;
}
`

function SlideBar() {
    return (
        <Slider>
            <NavLink to="/about"
                className={({ isActive }) => isActive ? "CurrentPage" : undefined}>Profile</NavLink>
            <NavLink to="/"
                className={({ isActive }) => isActive ? "CurrentPage" : undefined} end>Home</NavLink>
            <NavLink to="/projects"
                className={({ isActive }) => isActive ? "CurrentPage" : undefined}>Projects</NavLink>
        </Slider>
    )
}


export default SlideBar
