import React from 'react'
import styled from 'styled-components'
import Hello from './Hello'
import Waves from '../assets/generalimages/electronic-back.png'
import ButtonContact from './ButtonContact'

const CompletePage = styled.div`
    display: flex;
    justify-content:space-between;
    flex-direction:row;
    flex: 1;
    padding-left: 5vw;
    width:100%;
    padding-block: calc(1rem + 8vh);
`
const TextSide = styled.div`
    flex: 1;    
    width: 25%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
`
const ImageSide = styled.div`
    width:75%;
    display:flex;
    flex-direction:column;
    align-items:flex-end;
    justify-content:center;

img{
    width:100%;
}
`
const PresentationName = styled.div`
    position: relative;
    align-self: flex-end;
    color: white;
    font-family: var(--main-font);
    z-index: 3;

&::after{
    display: block;
    content: '';
    width: 110%;
    height: 40%;
    background: #5967e7a1;         ;
    position: absolute;
    top: 60%;
    left: -5%;
    z-index: -1;
}
`
const PresentationText = styled.div`
    color: white;
    font-family: var(--secundary-font);
    font-weight: 400;
`

function Main() {
    return (
        <CompletePage>
            <TextSide>
                <Hello />
                <PresentationName>I'm Leonardo Fernandes</PresentationName>
                <PresentationText>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Elit congue metus lobortis felis. Lectus risus ornare orci eget. Sit nam accumsan, nullam adipiscing donec lacinia eget vestibulum dui.</PresentationText>
                <ButtonContact />
            </TextSide>
            <ImageSide>
                <img src={Waves} alt="EletronicWaves"></img>
            </ImageSide>
        </CompletePage>
    )
}

export default Main
