import React from 'react'
import styled from 'styled-components'
import { motion } from 'framer-motion'
import { useEffect } from 'react'

const CategoryButton = styled(motion.p)`
`
const ButtonCategory = ({cat, setfilter, activefilter}) => {
    function CheckCat(cat) {
        if (activefilter.includes(cat)) {
            activefilter = activefilter.filter(item => item !== cat)
        } else {
            activefilter.push(cat)
            activefilter = activefilter.filter(item => item !== 'All')
        }
        if (cat === 'All') {
            activefilter = []
        }
        activefilter.length > 0  ? setfilter([...activefilter]) : setfilter(['All'])
    }



    const CategoryCollorsMatch = {
        'All' : '#5265FF',
        'WebDev' : "#BC2893",
        'BioTech' : "#19A49B",
        'DataScience' : "#E08E2E"
    }

    return (
        <CategoryButton
            style={activefilter.includes(cat) ? {color:CategoryCollorsMatch[cat]} : {color: '#FFFFFF'}}
            whileHover={{
                scale: 1.05,
                'text-shadow': `0px 0px 15px ${CategoryCollorsMatch[cat]}`,
                cursor: "pointer",
                transition: { duration: 0.1 },
            }}
            whileTap={{ scale: 0.9 }}
            onClick={() => CheckCat(cat)}
            >
            {cat}
        </CategoryButton>
    )
}

export default ButtonCategory
